import React from 'react';
import Axios from "axios";
import User from "../User/User";

import './Users.scss';
import '../../../../Shared/Scss/buttons.scss';
import Button from "@material-ui/core/Button";
import adolphPhoto from '../../../../Shared/images/users/user-adolph-2x.png';
import alexanderPhoto from '../../../../Shared/images/users/user-alexander-2x.jpg';
import elizabethPhoto from '../../../../Shared/images/users/user-elizabeth-2x.jpg';
import liamPhoto from '../../../../Shared/images/users/user-liam-2x.jpg';
import masonPhoto from '../../../../Shared/images/users/user-mason-2x.jpg';
import noahPhoto from '../../../../Shared/images/users/user-noah-2x.jpg';
import detect from "detect.js";

export default class Users extends React.Component {
    constructor(props) {
        super(props);
        const maxMobileSize = 755;

        this.state = {
            nextPage: 1,
            usersPerPage: 6,
            users: [],
            fullUsersList: false,
            isLoading: false
        };

        const userAgent = detect.parse(navigator.userAgent);
        this.isNeedToFix = /Windows|Android/i.test(userAgent.os.family);
        this.isFirefox = /Firefox/i.test(userAgent.browser.family);

        this.users = [
            {
                photo: noahPhoto,
                name: 'Noah',
                position: 'Leading specialist of the Control Department',
                email: 'noah.controldepartment@gmail.com',
                phone: '+38 (050) 678 03 24',
            },
            {
                photo: adolphPhoto,
                name: 'Adolph Blaine Charles David Earl ',
                position: 'The contextual advertising specialist',
                email: 'adolph.blainecharles-davidearl@example.com',
                phone: '+38 (095) 556 08 45',
            },
            {
                photo: liamPhoto,
                name: 'Liamgrievescasey Smith Wiam',
                position: 'Lead designer',
                email: 'liamgrievescasey@example.com',
                phone: '+38 (050) 273 93 32',
            },
            {
                photo: elizabethPhoto,
                name: 'Elizabeth',
                position: 'Frontend developer',
                email: 'elisabet.frontend@gmail.com',
                phone: '+38 (095) 924 66 37',
            },
            {
                photo: alexanderPhoto,
                name: 'Alexander',
                position: 'Backend developer',
                email: 'alexander.backend@gmail.com',
                phone: '+38 (050) 273 93 32',
            },
            {
                photo: masonPhoto,
                name: 'Mason',
                position: 'QA',
                email: 'mason.qa@gmail.com',
                phone: '+38 (095) 283 27 00',
            }
        ];

        this.correctUsersPerPageChecker = () => {
            if (document.body.clientWidth > maxMobileSize) {
                this.setState({
                    usersPerPage: 6,
                });
                return 6;
            };

            this.setState({
                usersPerPage: 3,
            });
            return 3;
        };

        this.getUsers = (page, usersPerPage) => {
            if(this.state.fullUsersList || this.state.isLoading) return;
            this.setState({ isLoading: true });

            Axios.get(`https://frontend-test-assignment-api.abz.agency/api/v1/users?page=${page}&count=${usersPerPage}`).then(res => {
                const newUsers = res.data.users;
                const fullUsersList = res.data.page >= res.data.total_pages;
                this.setState((state) => {
                    const users = state.users.concat(newUsers);

                    return {
                        fullUsersList: fullUsersList,
                        nextPage: ++state.nextPage,
                        users: users,
                    }
                });
            });

            this.setState({ isLoading: false });
        };
    }

    componentDidMount() {
        const usersPerPage = this.correctUsersPerPageChecker();
        this.getUsers(this.state.nextPage, usersPerPage);

        window.addEventListener("resize", this.correctUsersPerPageChecker);
    }

    render() {
        const contentText = this.props.contentText;
        const usersJSX = this.state.users.map((user, index) => {
            return <User key={index} user={user} />
        });

        return (
            <section className="users">
                <div className="users__container">
                    <h2 className={`users__title heading2${this.isNeedToFix || this.isFirefox ? ' overpassFix' : ''}`}>{contentText.title}</h2>
                    <h5 className="users__subtitle heading5">{contentText.subtitle}</h5>
                    <div className="users__list">
                        {usersJSX}
                    </div>
                    <div onClick={() => this.getUsers(this.state.nextPage, this.state.usersPerPage)} className="users__button-show-more button">
                        <Button
                            variant="contained"
                            className={`button-item button-item_secondary${this.state.fullUsersList || this.state.isLoading ? ' disabled' : ''}`}
                            disabled={this.state.fullUsersList}
                        >
                            {contentText.buttonShowMore}
                        </Button>
                    </div>
                </div>
            </section>
        )
    }
}